/* ---- grid-item ---- */
.grid-item {
  min-width: 480px;
  min-height: 120px;
  float: left;
  background: #D26;
  border: 1px solid #333;
  border-color: hsla(0, 0%, 0%, 0.5);
  /* border-radius: 57px; */
  padding: 4px;
  /* overflow-y: scroll; */
  margin-bottom: 24px;
  margin:2px;
}
.grid-item--width2 {
  width: 320px;
}
.grid-item--width3 {
  width: 480px;
}
.grid-item--width4 {
  width: 640px;
}
.grid-item--height2 {
  height: 200px;
}
.grid-item--height3 {
  height: 260px;
}
.grid-item--height4 {
  height: 360px;
}



